/* Primary Styles */
body,
html,
* {
	box-sizing: border-box;
	border-radius: 3px;
	
	
}
body,
html {
	margin: 0;
	padding: 0;
}

/* Header & Footer */
header,
footer {
	padding: 0.5rem;
}
header {
	// border-bottom: 5px solid red;
}
footer {
	border-top: 5px solid red;
}
ul li {
	white-space: normal;
}

/* Menu */
.menu {
	list-style: none;
	margin: 0;
	padding: 0;
	font-family: var(--heading-font);
	@media screen and (max-width: 700px) {
		white-space: pre-wrap;
	}
	li {
		display: inline-block;
		padding: 0.25em 0.5em;
		a {
			font-size: 1.5rem;
			padding: 0.5em;
			font-weight: 700;
			border: 3px solid transparent;
			&:hover {
				border: 3px solid var(--primary-color);
			}
		}
	}
}

/* Images */
.logo {
	height: 80px;
	width: auto;
}
.logobl{
	height :70px;
	width:auto;
	border-radius: 50%;
}
.logo2 {
	height: 100px;
	width: auto;
}
.img-fluid{
	height: 20px;
	width: 50px;
}
.qr-code {
	max-height: 150px;
	margin: 0.5rem;
}
.social-icon {
	width: 2.5rem;
	margin: 0.25em;
}

/* Tell Your Friends */
.tell-your-friends {
	text-align: center;
	> div {
		display: inline-block;
		vertical-align: middle;
	}
}

