/* Release Notes */
.date {
	color: var(--primary-color);
}
span {
	&.version {
		color: #aaa;
		font-family: var(--body-font);
		font-weight: normal;
		vertical-align: middle;
		margin: 0 10px;
		font-size: 14px;
	}
	&.tag {
		font-size: 12px;
		font-family: var(--body-font);
		font-weight: normal;
		background-color: #ccc;
		padding: 0.5em 1em;
		vertical-align: middle;
		border-radius: 12px;
		&.release {
			background-color: var(--release-color);
		}
		&.bugfix {
			background-color: var(--bugfix-color);
		}
	}
}
